/* small screen */
.container {
    margin: 5rem auto;
    padding: 1rem;
}
.container__title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.country__title {
    color: #06D6A0;
    font-style: normal;
    font-weight: 500;
    font-size: 61px;
    margin-top: 0.5em;
}
.country__subtitle {
    color: #118AB2;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 30px;
}
.table__covid {
    overflow-x:auto;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    border: 1px solid #ddd;
}
th {
    color: #06D6A0;
}
td {
    color: #64748B;
}
th, td {
    text-align: center;
    padding: 8px;
    border: 1px solid #ddd;
}

/* Large Screen */
@media (min-width: 992px) {
    table {
        width: 50%;
    }
    .table__covid {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}