/* Small Screen */
.container {
    /* margin: 5rem auto; */
    background: #F8F9FA;
}

.hero {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.hero__left {
    margin-bottom: 1rem;
    padding: 2rem;
}

.hero__title {
    color: #06D6A0;
    margin-bottom: 1rem;
    font-size: 2.44rem;
    align-items: center;
    justify-content: center;
    display: flex;
}

.hero__genre {
    color: #b5179e;
    margin-bottom: 1rem;
    font-size: 1.59rem;
}

.hero__description {
    color: #64748b;
    margin-bottom: 1rem;
}

.hero__button {
    padding: 0.8rem 2rem;
    border: none;
    border-radius: 10px;
    background-color: #06D6A0;
    color: #fff;
}

.hero__image {
    max-width: 100%;
    height: auto;
    border-radius: 25px;
}

.add-movie-container{
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}

input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

input[type=submit] {
    width: 100%;
    background-color: #06D6A0;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 15px;
    cursor: pointer;
}

input[type=submit]:hover {
    background-color: #4190cc;
}

label{
    display: flex;
    justify-content: start;
}

/* Medium Screen */

@media (min-width: 320px) and (max-width: 768px) {
    /*
    * Nothing TODO Here.
    * We dont change style Hero. 
    */
    .hero__image{
        display: none;
    }
}

@media (min-width: 768px) {
    /*
    * Nothing TODO Here.
    * We dont change style Hero. 
    */
}

/* Large Screen */
@media (min-width: 992px) {
    .container {
        max-width: 100%;
        height: 550px;
        background: #F8F9FA;
        padding: 5rem;
    }

    .hero {
        margin: 0 1rem;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        text-align: left;
    }

    .hero__left {
        flex-basis: 40%;
    }

    .hero__right {
        flex-basis: 60%;
    }
}
  