/* Small Screen */
.container {
    background-color: #06D6A0;
    padding: 1rem;
    color: #fff;
    display: flex;
    justify-content: space-between;
}

.navbar__brand {
    font-size: 2.4rem;
    margin-bottom: 1rem;
}

.navbar__list {
    display: flex;
    flex-direction: column;
    list-style: none;
}

.navbar__item {
    margin-bottom: 1rem;
    text-decoration: none;
    /* font-size: 25px; */
}

.hamburger{
    display: fixed;
    padding-top: 10px;
    margin-left: 10px;
    z-index: 6;
}

/* Medium Screen */
@media (min-width: 768px) {
    .navbar__brand {
        margin-bottom: 0;
    }

    .navbar__list {
        flex-direction: row;
    }

    .navbar__item {
        margin: 0 1rem;
    }

    .hamburger{
        display: none;
        z-index: 6;
    }
}


