/* Small Screen */
.container {
    /* margin: 5rem auto; */
    background: white;
}

.container__title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.countries {
    text-align: center;
}

.country__imagecontainer {
    height: 70%; 
    width: 70%;
    object-fit: contain;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    border-radius: 6px;
}

.country__image {
    height: 100%; 
    width: 100%;
    object-fit: contain;
}

.country__title {
    color: #06D6A0;
    font-style: normal;
    font-weight: 500;
    font-size: 61px;
    margin-top: 1em;
}

.country__subtitle {
    color: #118AB2;
    font-size: 25px;
    font-weight: 400;
    margin-bottom: 30px;
}

.country__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
}

/* Medium Screen */
@media (min-width: 768px) {
    .country__container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    }
}

/* Large Screen */
@media (min-width: 992px) {
    .container {
        max-width: 100%;
        height: auto;
        background: white;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}
  