/* Small Screen */
.country {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    margin: 15px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 38px;
    width: 100%;
}

.country__title {
    color: #4361ee;
    font-size: 39px;
    margin-bottom: 0.5rem;
}

.country__count {
    color: #64748b;
    font-size: 50px;
}

/* Medium Screen */
@media (min-width: 768px) {
    .country {
    flex-basis: 50%;
    }
}

/* Large Screen */
@media (min-width: 992px) {
    .country {
    flex-basis: 25%;
    padding: 1rem;
    }
}
  